/*Portrait*/
@media screen and (max-aspect-ratio: 16/9)
{
    .d-block
    {
        height: 30vh !important;
        margin: 0 0 1vh 0;
    }
}

/*Landscape*/
@media screen and (min-aspect-ratio: 1/1)
{
    .d-block
    {
        height:60vh !important;
        margin: 0 0 1vh 0;
    }

    .carousel
    {
        width: 80%;
        margin: 0 auto;
    }
}

