.product-information-container
{
    padding: 5vh 2vw 0 2vw;
    width: 100vw;
}

.product-information
{
    padding: 2vh 2vw 5vh 2vw;
}

/*Portrait*/
@media screen and (max-aspect-ratio: 16/9)
{
    .product-image-container, .product-information, .product-image
    {
        width: 100%;
    }
}

/*Landscape*/
@media screen and (min-aspect-ratio: 1/1)
{
    .product-information-container
    {
        display: flex;
    }

    .product-image-container, .product-information, .product-image
    {
        width: 45vw;
    }
}
