/*Portrait*/
@media screen and (max-aspect-ratio: 16/9)
{
    .container
    {
        width: 100%;
        height: 100%;
        margin: 0 0 10vw 0;
        padding: 0;
        max-width: none;
        display: table;
    }

    .container-image
    {
        display: table-header-group;
    }

    .container-content
    {
        display: table-footer-group;
    }

    .content
    {
        padding: 0 2vw 0 2vw;
    }
    .content-text
    {
        font-size: 0.9rem;
    }

    .content-title
    {
        margin-bottom: 8px;
        font-size: 2em;
        color: rgb(1, 115, 203);
    }
}

/*Landscape*/
@media screen and (min-aspect-ratio: 1/1)
{
    .container
    {
        width: 100%;
        margin: 0;
        padding: 0;
        max-width: none;
        display: flex
    }

    .container-panel
    {
        width: 50%;
        display: inline-block;
    }

    .container-content
    {
        padding: 0 2vw 0 5vw;
        width: 43vw;
        flex: 1;
    }

    .content
    {
        display: table;
        height: 100%;
    }

    .content-child
    {
        display: table-cell;
        vertical-align: middle;
    }

    .content-title
    {
        margin-bottom: 17px;
        font-size: 2em;
        color: rgb(1, 115, 203);
    }
}

