.center-text
{
    text-align: center;
    margin: 2vh 0 2vh 0;
    color: rgb(1, 115, 203);
    font-size: 1.2rem;
    font-weight: 500;
}

.information-container
{
    background-color: rgb(1, 115, 203);
    width: 100%;
    height: 70vh;
    justify-content: center;
    align-items: center;
}


.contact-form
{
    width: 100%;
}

.information
{
    margin: 0;
    width: 100%;
}


.send-email
{
    margin: 0;
    width: 100%;
}


.form-group
{
    text-align: center;
}

.form-label
{
    color: white;
    font-size: 1.2rem;
}

.btn
{
    color: rgb(1, 115, 203);
    background-color: white;
    border-color: white;
    font-size: 1.2rem;

}

.form-control
{
    width: 70%;
    color: rgb(1, 115, 203);
}



input
{
    margin: 0 auto;
    font-size: 1.2rem;
}

.contact-logo
{
    width: 10vw;
}

.phone, .mail
{
    display: flex;
    text-align: center;
    width: inherit;
    margin: 0 auto 3vh;

}

.icon
{
    width: 40%;
}

.info-data
{
    display: flex;
    color: white;
    font-size: 1.3rem;
    justify-content: center;
    align-items: center;
}

/*Portrait*/
@media screen and (max-aspect-ratio: 16/9)
{
    .form-control
    {
        width: 50%;
    }

    .phone
    {
        padding: 2vh 0 0 0;
    }
}

/*Landscape*/
@media screen and (min-aspect-ratio: 1/1)
{
    .information-container
    {
        display: flex;
        height: 42.5vh;
    }

    .information
    {
        width: 50%;
    }

    .contact-logo
    {
        width: 4vw;
    }

    .send-email
    {
        width: 50%;
        border-left-color: white;
        border-left-style: solid;
        border-left-width: 2px;
    }
}