.contact-container
{
    position: relative;
    height: calc(100vw * 0.406509253350351);
}

.contact-image
{
    width: 100%;
    position: absolute;
}

.image-centered-text
{
    width: 100%;
    height: 100%;
    position: absolute;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
    color: rgba(255,255,255,0.78);
}